@charset "utf-8";

// Import a Google Font
@import url('https://fonts.googleapis.com/css?family=Nunito:400,700');

// Set your brand colors

$beige-lighter: #eff0eb;

// Update Bulma's global variables
//$family-sans-serif: 'Nunito', sans-serif;

// Import only what you need from Bulma
@import '../node_modules/bulma/sass/utilities/_all.sass';
@import '../node_modules/bulma/sass/base/_all.sass';
@import '../node_modules/bulma/sass/helpers/_all.sass';
@import '../node_modules/bulma/sass/elements/button.sass';
@import '../node_modules/bulma/sass/elements/container.sass';
@import '../node_modules/bulma/sass/form/_all.sass';
@import '../node_modules/bulma/sass/elements/box.sass';
@import '../node_modules/bulma/sass/elements/title.sass';
@import '../node_modules/bulma/sass/elements/button.sass';
@import '../node_modules/bulma/sass/components/navbar.sass';
@import '../node_modules/bulma/sass/layout/section.sass';
@import '../node_modules/bulma/sass/elements/icon.sass';
@import '../node_modules/bulma/sass/elements/tag.sass';
@import '../node_modules/bulma/sass/components/modal.sass';
@import '../node_modules/bulma/sass/grid/columns.sass';
@import '../node_modules/bulma/sass/components/dropdown.sass';


@media (max-width: $tablet) {
  .section {
    padding: 3rem 0.5rem; } }

.text-overflow-rtl {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  direction: rtl;
  min-width: 10px;
  max-width: 75%; }

.text-overflow-ltr {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  direction: ltr;
  max-width: inherit; }


.has-mid-algm {
  display: flex;
  align-items: center; }

.put-right {
  margin-left: auto; }

.put-left {
  margin-right: 0;
  margin-left: 0; }


.container-login {
  @extend .container;
  max-width: 600px; }

.modal-buttons {
  @extend .buttons;
  justify-content: flex-end; }

.is-fab {
  width: 100%;
  position: fixed;
  bottom: 5%; }

.has-content-centered {
  display: flex;
  justify-content: center; }


.footer-buttons {
  justify-content: space-around;
  width: 100%; }


.cargando-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width : 100%;
  min-height: 80vh;
  height: 100%; }


.absolute-center-80vh {
  display: flex;
  justify-content: center;
  align-items: center;
  width : 100%;
  height: 100%;
  min-height: 80vh; }

.absolute-center {
  display: flex;
  justify-content: center;
  align-items: center;
  width : 100%;
  height: 100%; }

.mid-just {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.has-shadow {
  box-shadow: 10px 10px 10px -5px #CCCCCC; }


.resumen {
  background: $primary;
  margin-top: 0.75em;
  padding-left: 1em;
  padding-right: 1em;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  margin-bottom: 5px !important;
  .heading {
    @extend .is-size-7;
    color: $beige-lighter; }
  .title {
    @extend .is-size-6;
    color: $white; } }

.dropdown-trigger {
  width: 100% !important; }

.dropdown {
  width: 100% !important; }

.w100 {
  width: 100%; }

.w300 {
  width: 300px; }

.mw75 {
  max-width: 75%; }

.mw85 {
  max-width: 300px; }

.mw100 {
  max-width: 100%; }

.switch {
  min-width: 80px; }

.modal-content {
  padding: 1rem; }

.icon-clickable {
  pointer-events: auto !important; }

.spacer-end-list {
  height: 4rem; }




//ESPECIFICOS


.box-row {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.list-box {
  @extend .box;
  padding: 0.75em !important;
  margin-bottom: 0.5em !important;
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.list-image {
  width: 50px !important;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center; }

.list-content {
  display: flex;
  flex-direction: column;
  flex-grow: 100;
  flex-shrink: 100;
  padding-left: 1em; }

.titulo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 3px;
  padding-bottom: 12px !important;
  border-bottom: 1px solid $grey-lighter; }

.image-titulo-container {
  width: 250px;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center; }


.datos {
  padding-bottom: 12px !important;
  border-bottom: 1px solid $grey-lighter;
  padding-left: 0.75em !important;
  padding-right: 0.75em !important; }

.descripcion {
  margin-top: 1em;
  flex-grow: 1000 !important;
  padding-bottom: 12px !important;
  border-bottom: 1px solid $grey-lighter; }

.imagenes {
  margin-top: 1em;
  flex-grow: 100 !important; }

.cont-flex {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding-top: 1em; }

.cont-flex > div {
  padding: 3px;
  flex-grow: 1;
  min-width: 350px; }

.image {
  width: 100px;
  height: 100px;
  margin-left: 10px;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center; }

.btn-guardado {
  margin: 1.5em; }


.btn-add {
  width: 4em; }

.image-upload {
  z-index : 1;
  position: absolute; }

.image-titulo {
  max-width: 100%;
  max-height: 100%; }

.image-extra {
  max-width: 100% !important;
  max-height: 100%; }
